import React, { Component } from 'react'
import { connect } from 'react-redux'
import styled, { css } from 'styled-components';

export const Grade = () => {
    const GradeList = styled.section`
        height : 100%;
        width: calc(100% - 250px);
        padding : 200px 0px 0px 0px;
        flex: none;
        margin-top : 100px;

        header {
            height : 40px;
            margin-bottom: 38px;
            width : 100%;
        }

        header h2 {
            width : 100%;
            margin-bottom: 12px;
            font-size: 22px;
            line-height: 26px;
            font-weight: 800px;
        }

        header h3 {
            width : 100%;
            margin-bottom: 12px;
            font-size: 18px;
            line-height: 26px;
            font-weight: 800px;
        }

        header ul {
            width : 100%;
            margin-bottom: 28px;
        }

        header ul li {
            display: inline-block;
            margin-right: 16px;        
        }

        ul {
            margin-bottom: 28px;
        }

        ul, li {
            list-style: none;
        }

        table {
            width : 100%;
            border-collapse: collapse;
            table-layout: fixed;
        }

        table colgroup col{
            width: 15%;
        }

        table th {
            height: 52px;
            border-bottom: 1px solid #000000;
            vertical-align: middle;
            font-weight: normal;
        }

        table td {
            height: 70px;
            padding: 10px;
            box-sizing: border-box;
            border-bottom: 1px solid #000000;
            text-align: center;
            vertical-align: middle;
            word-break: break-all;
        }
    `

    const GradeHeader = styled.div`
        height: 200px; 
        width: 100%;
        background-color: #f8f8f8;
        color: black;
        margin-bottom: 38px;

        .gradeHeader {
            padding: 30px;
            display: table;
            width: 100% !important;
            height: 100%;
        }

        .gradeHeader .headerMain {
            position: relative;
            vertical-align: middle;
            display: table-cell;
            text-align: left;
            
        }

        .gradeHeader .headerMain .headerMainTable {
            display: table;
            width: 100%;
            
        }

        .gradeHeader .headerMain .headerMainTable div{
            display: table-cell;
            padding-right: 24px;
            vertical-align: middle;
        }

        .gradeHeader .headerMain .headerMainTable .profileImg {
            width: 100px;
            height: 100px;
        }


        .gradeHeader .headerPoint {
            position: relative;
            width: 60%;
            vertical-align: middle;
            display: table-cell;
            text-align: center;
        }

        .gradeHeader .headerPoint::before {
            content: '';
            display: block;
            width: 1px;
            height: 100px;
            position: absolute;
            background: #e5e5e5;
            left: 0;
            top: 50%;
            margin-top: -50px;
        }
    `

    return (
        <GradeList>
            <header className="gradeListHeader">
                <h2 className="headerTitle">
                     
                </h2>
            </header>
            <GradeHeader>
                <div className="gradeHeader">
                    <div className="headerMain">
                        <div className="headerMainTable">
                            <div className="profileImg">
                                img
                                 img
                            </div>
                            <div className="profileDetails">
                                    <p>  . </p>
                                    <p>  .</p>
                                    <p>  : 0</p>
                                    <p>  : 0</p>
                            </div>
                        </div>
                    </div>
                    <div className="headerPoint">
                        <p> 10   30000    .</p>
                        <span>( : 2020.06.01 ~ 2020.11.30     )</span><br/>
                        <span> 1           .</span>
                    </div>
                </div>
            </GradeHeader>
            <header className="gradeListHeader">
                <h3 className="headerTitle">
                   
                </h3>
            </header>
            <table className="orderListTable">
                <colgroup className="orderListColGroup">
                    <col className="orderListCol"></col>
                    <col className="orderListCol"></col>
                    <col className="orderListCol"></col>
                </colgroup>
                <thead className="orderListTableHead">
                    <tr className="orderListTableRow">
                        <th scope="col">
                            <div className="tableDiv">
                                 
                            </div>
                        </th>
                        <th scope="col">
                            <div className="tableDiv">
                                 
                            </div>
                        </th>
                        <th scope="col">
                            <div  className="tableDiv">
                                 
                            </div>
                        </th>
                    </tr>
                </thead>
				<tbody>
				<tr>
					<th scope="row">LV.1 </th>
					<td> ~ 2,000</td>
                    <td>1 </td>
				</tr>
				<tr>
					<th scope="row">LV.2 </th>
					<td>2,001 ~ 10,000</td>
                    <td>2 </td>
				</tr>
				<tr>
					<th scope="row">LV.3 </th>
					<td>10,001 ~ 100,000</td>
                    <td>3 </td>
				</tr>
				<tr>
					<th scope="row">LV.4 </th>
					<td>100,001 ~ 200,000</td>
                    <td>4 </td>
				</tr>
				<tr>
					<th scope="row">LV.5 </th>
					<td>200,001 ~ 500,000</td>
                    <td>5 </td>
				</tr>
				<tr>
					<th scope="row">LV.6 </th>
					<td>500,001 ~ 1,000,000</td>
                    <td>6</td>
				</tr>
				<tr>
					<th scope="row">LV.7 </th>
					<td>1,000,001 ~ 2,000,000</td>
                    <td>7</td>
				</tr>
				<tr>
					<th scope="row">LV.8 </th>
					<td>2,000,001 ~</td>
                    <td></td>
				</tr>
				</tbody>
			</table>
        </GradeList>
    )
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = {
    
}

export default connect(mapStateToProps, mapDispatchToProps)(Grade)
